
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'DialogTrade',
props: {
isShowTrade: {
type: Boolean,
default: false,
required: true
},
tradeTitle: {
type: String,
default: '',
required: true
}
},
components: {
XButton
},
data () {
return {
password: ''
}
},
methods: {
closeMyself() {
this.$emit("on-close")
},
tradeBtn () {
var that = this
let [password] = [that.password]
this.$emit("trades-btn",{password:password})
}
},
watch:{
isShowTrade: function (newData, oldData) {
this.password = ''
}
}
}



